<template>
  <div class="goodsinfo">
    <!--  -->

    <div class="mui-card">
      <div class="mui-card-content">
        <div class="mui-card-content-inner">
          <mui_ui></mui_ui>
        </div>
      </div>
    </div>

    <transition @before-enter="beforeEnter" @enter="enter" @after-enter="afterEnter">
      <div class="ball" v-show="ballFlag" ref="ball"></div>
    </transition>

    <!--  -->
    <div class="mui-card">
      <div class="mui-card-header">HUAWEIMate20 128G</div>
      <div class="mui-card-content">
        <div class="mui-card-content-inner">
          <span>
            
            <del>2699</del>
          </span>&nbsp;&nbsp;&nbsp;&nbsp;
          <span>2199</span>
          <br>
          <br>
          <p>
            
            <number @getcount="getSelectedCount"></number>
          </p>
          <el-button type="primary"></el-button>
          <el-button type="danger" @click="addToShopping"></el-button>
        </div>
      </div>
    </div>

    <!--  -->
    <div class="mui-card">
      <div class="mui-card-header"></div>
      <div class="mui-card-content">
        <div class="mui-card-content-inner"></div>
      </div>
      <div class="mui-card-footer">
        <mt-button type="primary" size="large" plain @click="goDesc"></mt-button>
      </div>
      <div class="mui-card-footer">
        <mt-button type="danger" size="large" plain @click="goComment"></mt-button>
      </div>
    </div>
  </div>
</template>

<script>
import mui_ui from "../subcomponents/mui-ui";
import number from "../subcomponents/goossinif_number";

export default {
  name: "",
  data() {
    return {
      phone: [
        {
          img: require("../../assets/images/1.jpg"),
          id: 1
        },
        {
          img: require("../../assets/images/2.jpg"),
          id: 2
        },
        {
          img: require("../../assets/images/3.jpg"),
          id: 3
        },
        {
          img: require("../../assets/images/4.jpg"),
          id: 4
        }
      ],
      num1: 1,
      ballFlag: false, //
      selectedCount: 1 //1
    };
  },
  components: {
    mui_ui,
    number
  },
  methods: {
    handleChange(value) {
      console.log(value);
    },
    goDesc() {
      this.$router.push({ name: "goodsdesc" });
    },
    goComment() {
      this.$router.push({ name: "goodscomment" });
    },
    addToShopping() {
      //
      this.ballFlag = !this.ballFlag;
      // {
      //   id:1,
      //   count: this.selectedCount,
      //   price: '3199',
      //   selected: true
      // }
      
    },
    beforeEnter(el) {
      // 
      el.style.transform = "translate(0, 0)";
    },
    enter(el, done) {
      //
      const ballPosition = this.$refs.ball.getBoundingClientRect();
      //
      const badgePosition = document
        .getElementById("badgehzd")
        .getBoundingClientRect();

      const xDist = badgePosition.left - ballPosition.left;
      const yDist = badgePosition.top - ballPosition.top;

      // 
      el.offsetWidth;
      // 
      el.style.transform = "translate(160px, 366px)";
      // 
      el.style.transition = "all 1s ease";
      // done  afterEnter() 
      done();
    },
    afterEnter(el) {
      // 
      this.ballFlag = !this.ballFlag;
    },
    getSelectedCount(count) {
      // data 
      this.selectedCount = count;
    }
  }
};
</script>
<style lang='css' scoped>
.goodsinfo {
  background-color: #eee;
  position: relative;
}
.mui-card-header {
}
.mui-card-content-inner {
}
.mui-card-content-inner span:nth-child(1) {
}
.mui-card-content-inner span:nth-child(2) {
}
.mui-card-content-inner p {
}
.ball {
  width: 15px;
  height: 15px;
  border-radius: 50%;
  background-color: red;
  position: absolute;
  top: 349px;
  left: 78px;
  z-index: 99;
}
</style>


