<template>
  <div class="comment">
    <h3></h3>
    <hr>
    <textarea name id cols="20" rows="5" maxlength="120" placeholder="BB20"></textarea>
    <mt-button type="primary" size="large"></mt-button>

    <div class="cmt-list">
      <div class="cmt-item">
        <div class="cmt-tit">&nbsp;&nbsp;&nbsp;&nbsp;{{date | datefmt }}</div>
        <div class="cmt-body"></div>
      </div>
    </div>
    <div class="cmt-list">
      <div class="cmt-item">
        <div class="cmt-tit">&nbsp;&nbsp;&nbsp;&nbsp;{{date | datefmt }}</div>
        <div class="cmt-body"></div>
      </div>
    </div>

    <mt-button type="danger" size="large" plain></mt-button>
  </div>
</template>

<script>
export default {
  name: "",
  data() {
    return {
      date: new Date()
    };
  }
};
</script>
<style lang='css' scoped>
.comment h3 {
  font-size: 18px;
}
hr {
  margin-bottom: 2px;
}
textarea {
  margin: 0;
}
.cmt-list {
  margin: 10px 0;
}
.cmt-item {
  background-color: #ccc;
}
.cmt-tit {
  font-size: 13px;
  line-height: 30px;
}
.cmt-body {
  line-height: 30px;
  font-size: 13px;
  background-color: #fff;
  text-indent: 2em;
}
button {
  height: 40%;
}
</style>


