<template>
  <div class="photoinfo">
    <h3></h3>
    <p class="subtitle">
      <span>{{ date|datefmt }}</span>
      <span>0</span>
    </p>

    <!--  -->
    <div class="thumbnail">
      <img
        class="preview-img"
        v-for="(item, index) in list"
        :src="item.img"
        height="100"
        @click="$preview.open(index, list)"
        :key="item.src"
      >
    </div>
    <div class="content">
      
      <br>
      <br>
    </div>

    <!--  -->
    <cmt-box/>
  </div>
</template>

<script>
//
import comment from "../subcomponents/comment";
//
// import index from '../../public/index.js'

export default {
  name: "",
  data() {
    return {
      date: new Date(),
      list: [
        {
          img: require("../../assets/images/1.jpg"),
          w: 600,
          h: 400
        },
        {
          img: require("../../assets/images/2.jpg"),
          w: 600,
          h: 400
        },
        {
          img: require("../../assets/images/3.jpg"),
          w: 600,
          h: 400
        },
        {
          img: require("../../assets/images/4.jpg"),
          w: 600,
          h: 400
        },
        {
          img: require("../../assets/images/5.jpg"),
          w: 600,
          h: 400
        },
        {
          img: require("../../assets/images/6.jpg"),
          w: 600,
          h: 400
        }
      ]
    };
  },
  components: {
    "cmt-box": comment
  }
};
</script>
<style lang='css' scoped>
.photoinfo {
  padding: 3px;
  background-color: #fff;
  width: 100%;
  height: 100%;
}
.photoinfo h3 {
  font-size: 16px;
  color: #26a2ff;
  text-align: center;
  line-height: 50px;
}
.subtitle {
  display: flex;
  justify-content: space-between;
  font-size: 14px;
}
.content {
  font-size: 13px;
}
.thumbnail{
  border: 1px solid #000;
  box-shadow: 0 0  8px #999;
}
</style>


