<template>
  <div class="newsinfo">
   <h3 class="title"></h3>
   <p>
     <span>{{ date|datefmt }}</span>
     <span>0</span>
   </p>
   <hr>
   <!--  -->
   <div class="content">
     
   </div>

   <h3 class="title"></h3>
   <p>
     <span>{{ date|datefmt }}</span>
     <span>0</span>
   </p>
   <hr>
   <!--  -->
   <div class="content">
     
   </div>

   <!--  -->
   <comment-box></comment-box>
  </div>
</template>

<script>
//
import comment from '../subcomponents/comment'
  export default {
    name:'',
    data () {
      return {
        date:new Date()
      }
    },
    components:{//
      'comment-box':comment
    }
  }
</script>
<style lang='css' scoped>
.newsinfo{
  padding: 0 4px;
}
.title{
  font-size: 16px;
  text-align: center;
  margin: 15px 0px;
  color:red
}
p{
  font-size: 13px;
  color: #226aff;
  display: flex;
  justify-content: space-between;
}
.content{
  padding: 0 4px;
  font-size: 14px;
  color: #666;
}
</style>


